.page {
    position: fixed;
    top: 10vh;
    bottom: 10vh;
    height: 62vh;
    width: 100vh;
    padding: 0 2vh;

    left: 50%;
    transform: translate(-50%, 10%);

    background-color: #f7f7f7c9;

    overflow: hidden;
    display: flex;
    flex-direction: column;

    border-radius: 3vh;
    box-shadow: 0.5vh 0.5vh 0.5vh 0.5vh rgba(89, 89, 89, 0.565);
}

.titleWrap {
    margin-top: 3vh;
    font-size: 43px;
    font-weight: bold;
    color: #262626;
    text-align: center;
    white-space: nowrap;
}

.login_content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 3vh;
}

.networkImg {
    height: 41vh;
    margin-top: 3vh;
    position: relative;
    margin-right: 51vh;
}

.login_border {
    position: absolute;
    border-left : 0.2vh solid rgb(167, 167, 167);
    height: 47.5vh;
}

.contentWrap {
    margin-top: 3vh;
    display: flex;
    flex-direction: column;
    position: absolute;
    margin-left: 50vh;
}

.inputTitle {
    font-size: 17px;
    font-weight: bold;
    color: #262626;
    margin-bottom: 0.3vh;
}


.inputWrap {
    display: flex;
    border-radius: 10vh;
    padding: 1.2vh;
    margin-top: 0.3vh;
    background-color: white;
    border: 1px solid #e2e0e0;
    width: 40vh;
    height: 2.3vh;
    box-shadow: 0.2vh 0.2vh 0.2vh 0.2vh rgba(89, 89, 89, 0.565);
}

.inputWrap:focus-within {
    border: 1px solid #6387ff;
}

.input {
    width: 35vh;
    outline: none;
    border: none;
    height: 2.25vh;
    font-size: 15px;
    font-weight: 500;
}

.input::placeholder {
    color: #dadada;
}

.find {
    font-size: 12px;
    display: flex;
    justify-content: flex-end;
    margin-top: 2vh;
    margin-right: 1vh;
}

.errorMessageWrap {
    margin-top: 0.5vh;
    color: #ef0000;
    font-size: 12px;
    height: 0.1vh;
    margin-left: 1vh;
}

.buttons {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    margin-top: 30vh;
}

